:root {
  --primary-color: #2938df;
  --pressed-state-color: #404bbf;
  --headings-herobackground-color: #2e2f42;
  --body-text-color: #434455;
  --background-color: #fff;
  --light-background-color: #f4f4fd;
  --accent-color: #e7e9fc;
}

body {
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

address {
  font-style: normal;
}

li {
  list-style: none;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

button {
  all: unset;
  cursor: pointer;
  display: inline-block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

nav {
  z-index: 99;
  width: 100%;
  background: var(--primary-color);
}

.nav-wrapper {
  height: 70px;
  justify-content: left;
  align-items: center;
  margin: auto;
  padding: 0 30px;
  line-height: 70px;
  display: flex;
}

.nav-links {
  color: var(--background-color);
}

.nav-list {
  display: flex;
}

.nav-item:not(:last-child) {
  margin-right: 30px;
}

.data-container {
  justify-content: center;
  display: flex;
}

.movies-list {
  flex-wrap: wrap;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.movies-item {
  box-sizing: border-box;
  max-width: calc(25% - 20px);
  flex: calc(25% - 20px);
}

.movie-poster {
  width: 100%;
  height: auto;
}

.movie-title, .movie-description, .movie-rate {
  text-align: center;
}

